body {
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

#app {
  background-color: #fff;
  max-width: 800px;
  margin: 0 auto;
  padding: 10px;
  box-shadow: 0 0 10px #0000001a;
}

h1 {
  text-align: center;
  color: #343a40;
}

.title-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card {
  border: 1px solid #e3e6f0;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 10px;
  box-shadow: 0 0 5px #0000001a;
}

.card-header {
  color: #fff;
  background-color: #bcbdbe;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 10px;
}

.card-body {
  text-align: center;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
}

.button-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  display: flex;
}

.button-container button {
  text-overflow: ellipsis;
  word-wrap: break-word;
  white-space: normal;
  text-align: center;
  flex-grow: 1;
  min-width: 60px;
  max-width: 300px;
  font-size: 14px;
  overflow: hidden;
}

.card-body p {
  max-width: 80%;
  margin: 5px 0;
}

.card-body .input-container {
  gap: 10px;
  display: flex;
}

.log-body {
  white-space: pre-wrap;
  word-wrap: break-word;
  background-color: #f9f9f9;
  border-radius: 4px;
  margin-left: -40px;
  padding: 10px;
}

.hma-body {
  z-index: 1000;
  box-sizing: border-box;
  background-color: #fff;
  width: 80%;
  height: auto;
  padding: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

.hma-body .close-button {
  cursor: pointer;
  z-index: 1001;
  background: none;
  border: none;
  padding: 10px;
  font-weight: bold;
  position: absolute;
  top: 10px;
  right: 15px;
}

.description-up {
  margin: 3px 0;
  padding: 2px;
}

#footer {
  opacity: .1;
  text-align: center;
  width: 100%;
  padding: 10px 0;
  font-size: .7em;
  position: fixed;
  bottom: 0;
}

@media (width <= 768px) {
  .button-container button {
    font-size: 12px;
  }
}

@media (width <= 480px) {
  .button-container button {
    font-size: 10px;
  }
}
